* {
    padding: 0;
    margin: 0;
}

body {
    background-color: rgb(69, 51, 51);
    color: white;
}

.container {
    max-width: 700px;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 1.2rem 0;
}

header p {
    font-size: 1.8rem;
    font-weight: 700;
}

header ul {
    display: flex;
}
header ul li {
    list-style-type: none;
    margin-left: 1.5rem;
}
header ul li a {
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 800;
    padding-bottom: 5px;
    color: gray;
}

section {
    text-align: center;
}
section p {
    margin-bottom: 1.5rem;
    font-size: 2rem;
    font-weight: 600;
}
